<template>
    <div class="bigbox">
        <div class="head">
            <router-link tag="a" class="back" :to="{name:'index'}"></router-link>
            {{category}}
        </div>
        <van-pull-refresh v-model="isLoading" @refresh="onRefresh" :loosing-text="loosText">
        <div class="middle">
            <!-- 大banner的开始 -->
            <div class="banner">
                <img src="../../style/img/四个菜单/蛋糕2.webp" alt="">
            </div>
            <!-- 大banner的结束 -->
                <div class="first" v-for="item in goods" :key="item.gid">
                    <div class="tupian">
                        <img :src=ImgHost+item.gthumb alt="">
                    </div>
                    <!-- 商品信息的开始 -->
                    <div class="biaoji">
                        <!-- 名称 -->
                        <div class="mingzi">
                            <span class="name">{{item.gname}}</span>
                        </div>
                        <!-- 价钱 -->
                        <div class="price">
                            <span class="jiage">￥{{item.sale}} <i style="margin-left: 5px;font-size: 14px;color: #8D8D8D;text-decoration: line-through;">原价{{item.mprice}}</i></span>
                        </div>
                        <!-- 立即购买 -->
                        <div class="buy">
                            <button>立即购买</button>
                        </div>
                    </div>
                </div>
            <!-- 第一个列表的开始 -->
        </div>
        </van-pull-refresh>
        <div class="bottom">
            <div class="botttom-car">
                <i>1</i>
            </div>
            <span style="float: left;font-size: 0.32rem;color: #FF9201;margin-left: 1.6rem;line-height: 1rem;">￥136</span>
            <a href="#" style=" width: 2rem;height: 1rem; float: right;background-color: #FED700; color: #fff;text-align:center;line-height: 1rem">去结算</a>
        </div>

    </div>
</template>

<script>
    import {SUCCESS,FAIL,IMGHOSTNAME,HOSTNAME} from "../../config/base";
    import axios from 'axios';
    export default {
        name: "Fruitsale",
        data(){
            return{
                isLoading:false,
                id:0,
            category:null,
                goods:[],
            ImgHost:IMGHOSTNAME,
                current:1,
                limit: 2,
                total:0,
                loosText:'释放加载'
            }
        },
        methods:{
            onRefresh() {
                setTimeout(() => {
                    this.$toast('刷新成功');

                    this.limit++;

            if (this.total>=this.limit) {
                this.getGoods();
                this.isLoading = false;
            }else {
                this.isLoading=false;
                window.alert('我是有底线的');
                this.isLoading = false;
                return;
            }
                            }, 500);
                        },
            getGoods(){
                axios.get(HOSTNAME+'/api/cates/'+this.id,{
                    params:{
                        'limit':this.limit
                    }
                }) .then(res=>{
                     res=res.data;
                    let data = res.data;
                    if (res.code==SUCCESS&&data){
                        this.$nextTick(()=>{
                            this.isLoading = false;
                        });
                        this.category=data.cname;
                        this.goods=data.goods;
                        this.total=res.count;
                    }else {
                        window.console.log(FAIL);
                    }
                });
            },
            getCurrentCategory(){

            }
        },
        beforeMount() {
            this.id=this.$route.query.id;
            this.getGoods();
        }
    }
</script>

<style scoped>
    @import "../../style/css/fruitsale.css";
    .van-pull-refresh{
        height: 100%;
    }
    .bigbox{
        height: 100%;
    }
</style>
